import React from "react";
import ReactDOM from "react-dom";
import { createBrowserHistory } from "history";
import { HashRouter, Router, Route, Switch } from "react-router-dom";

import "assets/scss/material-kit-pro-react.scss?v=1.8.0";

// pages for this product
import AboutUsPage from "views/AboutUsPage/AboutUsPage.js";
import BlogPostPage from "views/BlogPostPage/BlogPostPage.js";
import BlogPostsPage from "views/BlogPostsPage/BlogPostsPage.js";
import ComponentsPage from "views/ComponentsPage/ComponentsPage.js";
import ContactUsPage from "views/ContactUsPage/ContactUsPage.js";
import EcommercePage from "views/EcommercePage/EcommercePage.js";
import LandingPage from "views/LandingPage/LandingPage.js";
import LoginPage from "views/LoginPage/LoginPage.js";

import PricingPage from "views/PricingPage/PricingPage.js";
import ProfilePage from "views/ProfilePage/ProfilePage.js";
import VideoPage from "views/VideoPage/VideoPage.js";
import ProductPage from "views/ProductPage/ProductPage.js";
import SectionsPage from "views/SectionsPage/SectionsPage.js";
import ShoppingCartPage from "views/ShoppingCartPage/ShoppingCartPage.js";
import SignupPage from "views/SignupPage/SignupPage.js";
import ErrorPage from "views/ErrorPage/ErrorPage.js";

ReactDOM.render(
  <HashRouter basename='/'>
    <Switch>
      <Route exact path="/profile/:id" component={ProfilePage} />
      <Route exact path="/video-page" component={VideoPage} />
      <Route exact path="/product-page" component={ProductPage} />
      <Route exact path="/sections" component={SectionsPage} />
      <Route exact path="/shopping-cart-page" component={ShoppingCartPage} />
      <Route exact path="/signup-page" component={SignupPage} />
      <Route exact path="/error-page" component={ErrorPage} />
      <Route exact path="/" component={LandingPage} />
    </Switch>
  </HashRouter>,
  document.getElementById("root")
);
